import React from 'react';
import Slider from '@material-ui/core/Slider';
import Input from '@material-ui/core/Input';
import {ModalContext} from "./modal/ModalProvider";
import './Download.css';


function create_png(img, scale) {
  let ctx = create_canvas(img)

  let width = img['width']
  let height = img['height']

  const step_w = Math.round(width / 24)
  const step_h = Math.round(height / 24)
  let arr = [...Array(24)].map(e => Array(24));

  let pos_h = Math.round(height / 24 / 2)
  for (let y = 0; y < 24; y++) {
      let pos_w = Math.round(width / 24 / 2)
      for (let x = 0; x < 24; x++) {
          arr[x][y] = get_rgba(ctx, pos_w, pos_h)
          pos_w += step_w
      }
      pos_h += step_h
  }

  let canvas = window.document.createElement('canvas');
  canvas.width = 24 * scale
  canvas.height = 24 * scale
  let ctx_tgt = canvas.getContext("2d");

  console.log(arr)
  for (let y = 0; y < 24; y++) {
    for (let x = 0; x < 24; x++) {
      // let color = arr[x][y]
      // let r = color[0]
      // let g = color[1]
      // let b = color[2]
      // let a = color[3] / 255
      // ctx_tgt.fillStyle = "rgba(" + r + "," + g + "," + b + "," + a + ")";
      ctx_tgt.fillStyle = arr[x][y]
      ctx_tgt.fillRect(x*scale, y*scale, scale, scale);
    }
  }

  let link = document.createElement('a');
  link.download = 'cyberdoggo.png';
  link.href = canvas.toDataURL()
  link.click();
}

function create_svg(img) {
  let ctx = create_canvas(img)

  let svg = document.createElementNS("http://www.w3.org/2000/svg", "svg")
  svg.setAttributeNS(null, "shape-rendering", "crispEdges")
  function getNode(x, y, c) {
    let e = document.createElementNS("http://www.w3.org/2000/svg", "rect");
    e.setAttributeNS(null, "width", "10");
    e.setAttributeNS(null, "height", "10");
    e.setAttributeNS(null, "x", x * 10);
    e.setAttributeNS(null, "y", y * 10);
    e.setAttributeNS(null, "fill", c);
    svg.append(e)
  }

  let width = img['width']
  let height = img['height']

  const step_w = Math.round(width / 24)
  const step_h = Math.round(height / 24)

  let pos_h = Math.round(height / 24 / 2)
  for (let y = 0; y < 24; y++) {
    let pos_w = Math.round(width / 24 / 2)
    for (let x = 0; x < 24; x++) {
      let color = get_rgba(ctx, pos_w, pos_h)
      getNode(x, y, color)
      pos_w += step_w
    }
    pos_h += step_h
  }

  var data = (new XMLSerializer()).serializeToString(svg);
  var DOMURL = window.URL || window.webkitURL || window;
  var svgBlob = new Blob([data], {type: 'image/svg+xml;charset=utf-8'});
  var link = document.createElement('a');
  link.download = 'cyberdoggo.svg';
  link.href = DOMURL.createObjectURL(svgBlob)
  link.click()
}

function create_canvas(img) {
  const canvas = window.document.createElement('canvas');
  let width = console.log(img['width'])
  let height = console.log(img['height'])
  const ctx = canvas.getContext('2d');
  ctx.width = width
  ctx.height = height
  ctx.drawImage(img, 0, 0)
  return ctx
}

function get_rgba(ctx, x, y) {
  let c = ctx.getImageData(x, y, 1, 1)
  let r = c.data[0].toString(16).padStart(2, '0')
  let g = c.data[1].toString(16).padStart(2, '0')
  let b = c.data[2].toString(16).padStart(2, '0')
  let a = c.data[3].toString(16).padStart(2, '0')
  return "#" + r + g + b + a;
}

const Download = ({img}) => {
  const [value, setValue] = React.useState(14);

  const handleBlur = () => {
    if (value === "" || value < 1)
      setValue(14)
  }

  const handleSliderChange = (event, newValue) => {
    setValue(newValue);
  };

  const handleInputChange = (event) => {
    setValue(event.target.value === "" ? "" : Number(event.target.value));
  }

  let { handleModal } = React.useContext(ModalContext)
  return (
    <div style={{maxWidth: "400px"}}>
      <h3 style={{margin: "0px"}}>Download your Doggo</h3>
      <p>Choose the right scale for your PNG image or download the image as an SVG.</p>
      <h5>Image Scale</h5>
      <div style={{display: "flex"}}>
        <Slider style={{flexGrow: "1", marginRight: "40px"}}
          value={typeof value === "number" ? value : 0}
          onChange={handleSliderChange}
          min={1}
          max={200}
        />
        <Input style={{width: "70px"}}
          value={value}
          margin="dense"
          onChange={handleInputChange}
          onBlur={handleBlur}
          inputProps={{
            type: "number"
          }}
        />
      </div>
      <h5>Resulting Image Size</h5>
      {value * 24} x {value * 24}
      <div style={{paddingTop: "25px"}}>
        <div className="download button" onClick={() => create_png(img, value)}>PNG</div>
        <div className="download button" onClick={() => create_svg(img)}>SVG</div>
        <div className="cancel button" onClick={handleModal}>Cancel</div>
      </div>
    </div>
  )
}

export default Download
